	import TopTar from "../../login/components/toptar.vue"
	export default{
		 components:{
		 		TopTar,

		 },
		 data(){
			 return{
				    name:"",
					phone:"",
					id:"",
					address:"",
					zsrc:"../../../static/user/user-authentication/zheng.jpg",
					fsrc:"../../../static/user/user-authentication/fan.jpg"
			 }
		 },
		 methods:{
			 selectimg(index){
				 var that=this;
				 uni.chooseImage({
				 	count:1,
					sizeType:"compressed",
					success:function(res){
						if(index==0){
							that.zsrc=res.tempFilePaths;
						}else{
							that.fsrc=res.tempFilePaths;
						}   
					}
				 })
			 },
			 submit(){
				 if(this.name==""){
				 				 uni.showToast({
				 				     title: '请输入姓名',
				 				     duration: 1000,
				 				 	 icon:"none"
				 				 });
				 				 return  false;
				 };
                if(this.phone==""){
                				 uni.showToast({
                				     title: '请输入手机号',
                				     duration: 1000,
                				 	 icon:"none"
                				 });
                				 return  false;
                }else if(!this.phone==""){
					var phonecheck=/^1([38]\d|5[0-35-9]|7[3678])\d{8}$/;
					if(!phonecheck.test(this.phone)){
						uni.showToast({
						    title: '请输入正确的手机号',
						    duration: 1000,
							 icon:"none"
						});
						return  false;
					}
				};
                if(this.id==""){
                				 uni.showToast({
                				     title: '请输入身份证',
                				     duration: 1000,
                				 	 icon:"none"
                				 });
                				 return  false;
                }else if(!this.id==""){
					var idcheck= /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/;
					if(!idcheck.test(this.id)){
						uni.showToast({
						    title: '请输入正确的身份证',
						    duration: 1000,
							 icon:"none"
						});
						return  false;
					}
				};
                if(this.address==""){
                				 uni.showToast({
                				     title: '请输入地址',
                				     duration: 1000,
                				 	 icon:"none"
                				 });
                				 return  false;
                } 
			 }
		 }
	}
